.title-section {
  margin: 0 auto;
  padding: 50px 0 50px 0;
  width: 80%;
  flex: 1 0 auto;
  /* height: 100%;
  min-height: calc(100vh - 110px - 180px); */
}
.title-section-home {
  margin: 0 auto;
  width: 93%;
  /* min-height: calc(100vh - 110px - 180px); */
}
.home-image{
  background-image: url('../../assets/20231026_120514.jpg');
  width: 100%;
  height: 650px;
  background-position-y: -200px;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
 

}
.home-title{
  top: 10%;
  font-family: "Pacifico",cursive;
  text-align: center;
  width: 40%;
  left:55%;
  position: relative;
  font-size: 40px;
  color: #0D3033;
  text-shadow: 3px 2px 2px rgba(153, 153, 153, 0.6)
}
.home-subtitle{
  /* margin-top: 2em;
  margin-left: 50px; */
  position: relative;
  left:10%;
  top: 10%;
  width: 40%;
  font-size: 20px;
  font-family: "Playfair Display";
  font-style: italic;
  font-weight: 400;
  color: #0D3033;
}
.home-info-title{
  text-align: center;
  padding-bottom: 20px;
  font-size: 54px;
  color: #0D3033;
  font-family: 'Jost', sans-serif;
  font-style: italic;
}
.home-info-content{
  margin: 0 auto;
  width: 750px;
  height: 450px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}
.home-info-content-column{
  height: 450px;
}
.home-info-content-column-left{
  width: 38%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.home-info-content-column-right{
  width: 58%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.home-info-content-img1{
  background-image: url('../../assets/101.jpeg');
  width: 100%;
  height: 450px;
  background-size: cover;
  background-repeat: no-repeat;
}
.home-info-content-img2{
  background-image: url('../../assets/20231026_114848.jpg');
  width: 100%;
  height: 200px;
  background-size: cover;
  background-repeat: no-repeat;
}
.home-info-content-img3{
  background-position-y: -20px;
  background-image: url('../../assets/20220610_190753.jpg');
  width: 200px;
  height: 200px;
  background-size: cover;
  background-repeat: no-repeat;
}
.home-info-content-img4{
  background-image: url('../../assets/20231026_120733.jpg');
  width: 200px;
  height: 200px;
  background-size: cover;
  background-repeat: no-repeat;
}
.home-img-column{
  display: flex;
  justify-content: space-between;
}
@media only screen and (max-width: 1207px) {
  .title-section {
    width: 90%;
  }
}

@media only screen and (max-width: 1048px) {
  .title-section {
    width: 95%;
  }
}

@media only screen and (max-width: 992px) {
  .title-section {
    width: 90%;
    min-height: calc(100vh - 110px - 165px);
  }
}

@media only screen and (max-width: 882px) {
  .title-section {
    width: 95%;
  }
}

@media only screen and (max-width: 834px) {
  .title-section {
    width: 97%;
    padding: 30px 0 30px 0;
    min-height: calc(100vh - 110px - 65px);
  }
}

@media only screen and (max-width: 480px) {
  .title-section {
    min-height: calc(100vh - 130px);
  }
}

@media only screen and (max-width: 322px) {
  .title-section {
    width: 98%;
    padding: 20px 0;
    min-height: calc(100vh - 170px);
  }
}