.helpful-title {
  text-align: center;
}

.helpful-h1 {
  text-align: center;
  color: var(--price-color);
  font-family: "Join";
  font-style: italic;
}
.helpful-h3 {
  text-align: center;
  color: var(--price-color);
  font-family: "Join";
  font-style: italic;
  margin-block-end: 0px;
  margin-block-start: 0px;
}
.helpful-info-container{
  margin: 30px 0;
}

@media only screen and (max-width: 1055px) {
  .helpful-info-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
  }
}

@media only screen and (max-width: 544px) {
  .helpful-h1 {
    font-size: 24px;
  }
}