.brand-title {
  text-align: center;
  color: var(--price-color);
  font-family: "Cormorant Garamond", serif;
  font-weight: 800;
  color: #0d3033;
}

.brand-section {
  margin: 0 auto;
  padding: 50px 0 50px 0;
  width: 80%;
  min-height: calc(100vh - 110px - 180px);
}
.brand-subtitle {
  text-align: center;
  color: var(--price-color);
  font-family: "Join", serif;
  color: #0d3033;
  padding: 10px 0 30px 0;
}
.brand-content-subtitle {
  font-size: 22px;
}
.brand-content-column-text:nth-child(1){
  width: 50%;
  text-align: center;
  margin-top: 30px;

}
.brand-content {
  font-size: 20px;
  font-weight: 600;
  text-align: justify;
  color: rgb(66, 37, 15);
  font-family: "Cormorant Garamond", sans-serif;
}
.brand-content-title {
  color: #0d3033;
  font-size: 30px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-style: italic;
  padding: 30px 0;
}
.brand-content-subtitle-color {
  background-color: #0d3033;
  position: relative;
  color: white;
  text-align: center;
  width: 75%;
  padding: 5px 15px; 
  margin: 0 auto;
  margin-top: -120px;
  margin-right: 20px;
  z-index: 1;
  margin-bottom: 20px;
}
.brand-content-pic1 {
  width: 520px;
  height: 290px;
  background-image: url("../../assets/20231026_113456.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}
.brand-content-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.brand-content-column-text:nth-child(2) {
  width: 55%;
  text-align: center;  
  margin-top: 16px;
}

.brand-content-pic2 {

  margin-bottom: 50px;
  width: 400px;
  height: 550px;
  background-image: url("../../assets/101.jpeg");
  background-repeat: no-repeat;
  background-size: cover  ;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}
.brand-content-subtitle-footer {
  text-align: center;
}
